import {css, html, LitElement} from 'lit';
import {customElement, property} from 'lit/decorators.js';
import './eb-attribution.js';

@customElement('eb-article-card')
export class ArticleCard extends LitElement {
  static override styles = css`
    :host {
      background: var(--fillColor, #ffffff);
      border: 1px solid rgba(0, 0, 0, 0.0578);
      background-clip: padding-box;
      border-radius: 4px;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
      box-sizing: border-box;
      color: var(--defaultColor, #000000);
      contain: content;
      content-visibility: auto;
      outline-offset: 1px;
    }
  `;

  @property({type: String}) override title = '';
  @property({type: String}) description = '';

  override render() {
    return html`
      <eb-attribution></eb-attribution>
      <h2>${this.title}</h2>
      <p>${this.description}</p>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'eb-article-card': ArticleCard;
  }
}
